.counter {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
  margin: 10px;
  padding: 10px;
  border: 1px solid #75d4f5;
  border-radius: 12px;
  color: #75d4f5;

  &__block {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}